import { useState } from 'react';
import './TechnologyCard.css';
//   
// import TechnologyResources from './TechnologyResources';

function TechnologyCard({ technology, onClick, onStatusUpdate }) {
  const [showNotes, setShowNotes] = useState(false);
  const [notes, setNotes] = useState(technology.notes);
  
  //   
  // const updateProgress = (newProgress) => {
  //   if (onStatusUpdate) {
  //     //      
  //     console.log('Update progress to:', newProgress);
  //   }
  // };

  const getStatusColor = (status) => {
    switch(status) {
      case 'planned': return '#f39c12';
      case 'in-progress': return '#3498db';
      case 'completed': return '#2ecc71';
      case 'paused': return '#e74c3c';
      default: return '#95a5a6';
    }
  };

  const getStatusText = (status) => {
    switch(status) {
      case 'planned': return '';
      case 'in-progress': return ' ';
      case 'completed': return '';
      case 'paused': return '';
      default: return status;
    }
  };

  const handleStatusClick = (e) => {
    e.stopPropagation();
    const nextStatus = getNextStatus(technology.status);
    if (onStatusUpdate) {
      onStatusUpdate(technology.id, nextStatus);
    }
  };

  const getNextStatus = (currentStatus) => {
    const statusFlow = {
      'planned': 'in-progress',
      'in-progress': 'completed',
      'completed': 'paused',
      'paused': 'planned'
    };
    return statusFlow[currentStatus] || 'planned';
  };

  const handleSaveNotes = (e) => {
    e.stopPropagation();
    if (onStatusUpdate) {
      //       
      console.log('Notes saved:', notes);
    }
    setShowNotes(false);
  };

  return (
    <div 
      className="technology-card" 
      onClick={onClick}
      role="button"
      tabIndex="0"
      aria-label={`  ${technology.title}. : ${getStatusText(technology.status)}. : ${technology.progress}%.   .`}
      onKeyPress={(e) => {
        if (e.key === 'Enter' || e.key === ' ') {
          e.preventDefault();
          onClick();
        }
      }}
    >
      <div className="card-header">
        <h3>{technology.title}</h3>
        <span 
          className="status-badge"
          style={{ backgroundColor: getStatusColor(technology.status) }}
          onClick={handleStatusClick}
          role="button"
          tabIndex="0"
          aria-label={` : ${getStatusText(technology.status)}.    .`}
          onKeyPress={(e) => {
            if (e.key === 'Enter' || e.key === ' ') {
              e.preventDefault();
              handleStatusClick(e);
            }
          }}
        >
          {getStatusText(technology.status)}
        </span>
      </div>
      
      <p className="description">{technology.description}</p>
      
      <div className="card-meta">
        <span className="category">{technology.category}</span>
        <span className="difficulty">: {technology.difficulty}</span>
      </div>
      
      <div className="progress-section">
        <div className="progress-label">
          <span>:</span>
          <span>{technology.progress}%</span>
        </div>
        <div className="progress-bar">
          <div 
            className="progress-fill" 
            style={{ 
              width: `${technology.progress}%`,
              backgroundColor: getStatusColor(technology.status)
            }}
            aria-valuenow={technology.progress}
            aria-valuemin="0"
            aria-valuemax="100"
            role="progressbar"
          />
        </div>
      </div>
      
      <div className="card-footer">
        <div className="dates">
          {technology.startedAt && (
            <span>: {technology.startedAt}</span>
          )}
          {technology.completedAt && (
            <span>: {technology.completedAt}</span>
          )}
        </div>
        
        <button 
          className="btn-notes"
          onClick={(e) => {
            e.stopPropagation();
            setShowNotes(!showNotes);
          }}
          aria-label={showNotes ? " " : " "}
          aria-expanded={showNotes}
        >
          {showNotes ? ' ' : ' '}
        </button>
      </div>
      
      {showNotes && (
        <div className="notes-section">
          <textarea
            value={notes}
            onChange={(e) => setNotes(e.target.value)}
            placeholder="   ..."
            onClick={(e) => e.stopPropagation()}
            aria-label="  "
          />
          <button 
            className="btn-save"
            onClick={handleSaveNotes}
            aria-label=" "
          >
            
          </button>
        </div>
      )}
      
      {technology.resources && technology.resources.length > 0 && (
        <div className="resources-section">
          <p className="resources-label">:</p>
          <ul>
            {technology.resources.slice(0, 2).map((resource, index) => (
              <li key={index}>
                <a 
                  href={resource} 
                  target="_blank" 
                  rel="noopener noreferrer"
                  onClick={(e) => e.stopPropagation()}
                  aria-label={` ${index + 1}   ${technology.title}`}
                >
                  {resource.length > 40 ? resource.substring(0, 40) + '...' : resource}
                </a>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
}

export default TechnologyCard;