import { useState } from 'react';
import useTechnologies from '../hooks/useTechnologies';
import './RoadmapImporter.css';

function RoadmapImporter() {
  const { addTechnology } = useTechnologies();
  const [importing, setImporting] = useState(false);
  const [apiUrl, setApiUrl] = useState('');
  const [importResult, setImportResult] = useState(null);

  //  API   
  const exampleRoadmaps = [
    {
      name: 'Frontend Roadmap',
      url: 'https://api.example.com/roadmaps/frontend',
      description: '   '
    },
    {
      name: 'Backend Roadmap',
      url: 'https://api.example.com/roadmaps/backend',
      description: '   '
    },
    {
      name: 'Fullstack Roadmap',
      url: 'https://api.example.com/roadmaps/fullstack',
      description: '  -'
    }
  ];

  // Mock    (     API)
  const mockRoadmapData = {
    frontend: [
      { 
        title: 'HTML5', 
        description: '  -',
        category: 'frontend',
        difficulty: 'beginner'
      },
      { 
        title: 'CSS3', 
        description: '  ',
        category: 'frontend',
        difficulty: 'beginner'
      },
      { 
        title: 'JavaScript ES6+', 
        description: ' JavaScript',
        category: 'frontend',
        difficulty: 'beginner'
      },
      { 
        title: 'React Hooks', 
        description: ' React  ',
        category: 'frontend',
        difficulty: 'intermediate'
      },
      { 
        title: 'Next.js', 
        description: 'React   ',
        category: 'frontend',
        difficulty: 'intermediate'
      }
    ],
    backend: [
      { 
        title: 'Node.js', 
        description: '  JavaScript',
        category: 'backend',
        difficulty: 'beginner'
      },
      { 
        title: 'Express.js', 
        description: '-  Node.js',
        category: 'backend',
        difficulty: 'beginner'
      },
      { 
        title: 'REST API', 
        description: ' RESTful API',
        category: 'backend',
        difficulty: 'intermediate'
      },
      { 
        title: 'PostgreSQL', 
        description: '  ',
        category: 'database',
        difficulty: 'intermediate'
      }
    ]
  };

  const handleImportRoadmap = async (roadmapType) => {
    try {
      setImporting(true);
      setImportResult(null);

      //    API
      await new Promise(resolve => setTimeout(resolve, 1500));

      const technologies = mockRoadmapData[roadmapType] || [];
      
      if (technologies.length === 0) {
        throw new Error('   ');
      }

      //   
      let importedCount = 0;
      for (const tech of technologies) {
        addTechnology(tech.title, tech.description, tech.category, tech.difficulty);
        importedCount++;
      }

      setImportResult({
        success: true,
        message: `  ${importedCount}   "${roadmapType}"  `,
        count: importedCount
      });

    } catch (err) {
      setImportResult({
        success: false,
        message: ` : ${err.message}`
      });
      console.error(' :', err);
    } finally {
      setImporting(false);
    }
  };

  const handleCustomImport = async () => {
    if (!apiUrl.trim()) {
      setImportResult({
        success: false,
        message: ' URL API'
      });
      return;
    }

    try {
      setImporting(true);
      setImportResult(null);

      //      fetch 
      // const response = await fetch(apiUrl);
      // const data = await response.json();

      await new Promise(resolve => setTimeout(resolve, 2000));

      // Mock   API
      const mockApiResponse = [
        { 
          title: 'GraphQL', 
          description: '   API',
          category: 'backend',
          difficulty: 'intermediate'
        },
        { 
          title: 'Apollo Client', 
          description: '  GraphQL',
          category: 'frontend',
          difficulty: 'intermediate'
        }
      ];

      let importedCount = 0;
      for (const tech of mockApiResponse) {
        addTechnology(tech.title, tech.description, tech.category, tech.difficulty);
        importedCount++;
      }

      setImportResult({
        success: true,
        message: ` ${importedCount}   API`,
        count: importedCount
      });

    } catch (err) {
      setImportResult({
        success: false,
        message: `    API: ${err.message}`
      });
    } finally {
      setImporting(false);
    }
  };

  return (
    <div className="roadmap-importer">
      <div className="importer-header">
        <h3>     API</h3>
        <p className="importer-subtitle">
               
        </p>
      </div>

      <div className="example-roadmaps">
        <h4>  :</h4>
        <div className="roadmap-cards">
          {exampleRoadmaps.map((roadmap, index) => (
            <div key={index} className="roadmap-card">
              <div className="roadmap-info">
                <h5>{roadmap.name}</h5>
                <p>{roadmap.description}</p>
              </div>
              <button
                onClick={() => handleImportRoadmap(roadmap.name.toLowerCase().split(' ')[0])}
                disabled={importing}
                className="import-btn"
              >
                {importing ? '...' : ''}
              </button>
            </div>
          ))}
        </div>
      </div>

      <div className="custom-import">
        <h4>   API:</h4>
        <div className="import-form">
          <input
            type="text"
            value={apiUrl}
            onChange={(e) => setApiUrl(e.target.value)}
            placeholder=" URL API   ..."
            className="api-input"
            disabled={importing}
          />
          <button
            onClick={handleCustomImport}
            disabled={importing || !apiUrl.trim()}
            className="custom-import-btn"
          >
            {importing ? '...' : ''}
          </button>
        </div>
        <p className="api-hint">
                   API. 
              .
        </p>
      </div>

      {importResult && (
        <div className={`import-result ${importResult.success ? 'success' : 'error'}`}>
          <div className="result-icon">
            {importResult.success ? '' : ''}
          </div>
          <div className="result-content">
            <p className="result-message">{importResult.message}</p>
            {importResult.success && importResult.count && (
              <p className="result-count">
                 : <strong>{importResult.count}</strong>
              </p>
            )}
          </div>
          <button
            onClick={() => setImportResult(null)}
            className="close-result"
          >
            
          </button>
        </div>
      )}

      <div className="importer-info">
        <div className="info-box">
          <h5>  :</h5>
          <ul>
            <li>1.    </li>
            <li>2.    API ()</li>
            <li>3.     </li>
            <li>4.      </li>
          </ul>
        </div>
        <div className="info-box">
          <h5> API  :</h5>
          <ul>
            <li><a href="https://github.com/public-api-lists/public-api-lists" target="_blank" rel="noopener noreferrer">Public API Lists</a></li>
            <li><a href="https://jsonplaceholder.typicode.com/" target="_blank" rel="noopener noreferrer">JSONPlaceholder</a></li>
            <li><a href="https://dummyjson.com/" target="_blank" rel="noopener noreferrer">DummyJSON</a></li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default RoadmapImporter;