import DataImportExport from '../components/DataImportExport';
import useTechnologies from '../hooks/useTechnologies';
import './SettingsPage.css';

function SettingsPage() {
  const { technologies, importTechnologies, resetAllStatuses, markAllCompleted } = useTechnologies();

  const handleImport = (importedTechnologies) => {
    importTechnologies(importedTechnologies);
  };

  const handleResetAll = () => {
    if (window.confirm(' ,      ?')) {
      resetAllStatuses();
    }
  };

  const handleMarkAllCompleted = () => {
    if (window.confirm(' ,       ?')) {
      markAllCompleted();
    }
  };

  return (
    <div className="settings-page">
      <div className="page-header">
        <h1></h1>
        <p>    </p>
      </div>

      <div className="settings-sections">
        <section className="settings-section" aria-labelledby="data-management-title">
          <h2 id="data-management-title"> </h2>
          <DataImportExport 
            technologies={technologies} 
            onImport={handleImport}
          />
        </section>

        <section className="settings-section" aria-labelledby="bulk-actions-title">
          <h2 id="bulk-actions-title"> </h2>
          <div className="bulk-actions">
            <button 
              onClick={handleResetAll}
              className="btn-warning"
              aria-label="   "
            >
                
            </button>
            <button 
              onClick={handleMarkAllCompleted}
              className="btn-success"
              aria-label="    "
            >
                 
            </button>
          </div>
          <p className="help-text">
            :    .     .
          </p>
        </section>

        <section className="settings-section" aria-labelledby="app-info-title">
          <h2 id="app-info-title">  </h2>
          <div className="app-info">
            <p><strong>:</strong>  </p>
            <p><strong>:</strong> 1.0.0</p>
            <p><strong>  :</strong> {technologies.length}</p>
            <p><strong> :</strong> {Math.round(technologies.reduce((sum, tech) => sum + tech.progress, 0) / technologies.length) || 0}%</p>
          </div>
        </section>
      </div>
    </div>
  );
}

export default SettingsPage;