import { useState, useEffect, useCallback } from 'react';
import './TechnologyForm.css';

function TechnologyForm({ onSave, onCancel, initialData = {} }) {
  //         
  const [formData, setFormData] = useState({
    title: initialData.title || '',
    description: initialData.description || '',
    category: initialData.category || 'frontend',
    difficulty: initialData.difficulty || 'beginner',
    deadline: initialData.deadline || '',
    resources: initialData.resources || ['']
  });

  //     
  const [errors, setErrors] = useState({});

  //    
  const [isFormValid, setIsFormValid] = useState(false);

  //    
  const [isSubmitting, setIsSubmitting] = useState(false);

  //   URL
  const isValidUrl = (string) => {
    try {
      new URL(string);
      return true;
    } catch (_) {
      return false;
    }
  };

  //      useCallback
  const validateForm = useCallback(() => {
    const newErrors = {};

    //   
    if (!formData.title.trim()) {
      newErrors.title = '  ';
    } else if (formData.title.trim().length < 2) {
      newErrors.title = '    2 ';
    } else if (formData.title.trim().length > 50) {
      newErrors.title = '    50 ';
    }

    //  
    if (!formData.description.trim()) {
      newErrors.description = '  ';
    } else if (formData.description.trim().length < 10) {
      newErrors.description = '    10 ';
    }

    //   (    )
    if (formData.deadline) {
      const deadlineDate = new Date(formData.deadline);
      const today = new Date();
      today.setHours(0, 0, 0, 0); //      

      if (deadlineDate < today) {
        newErrors.deadline = '     ';
      }
    }

    //  URL- 
    formData.resources.forEach((resource, index) => {
      if (resource && !isValidUrl(resource)) {
        newErrors[`resource_${index}`] = '  URL';
      }
    });

    setErrors(newErrors);
    setIsFormValid(Object.keys(newErrors).length === 0);
  }, [formData]);

  //      formData
  useEffect(() => {
    validateForm();
  }, [formData, validateForm]);

  //     (input, select, textarea)
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  //      
  const handleResourceChange = (index, value) => {
    const newResources = [...formData.resources];
    newResources[index] = value;
    setFormData(prev => ({
      ...prev,
      resources: newResources
    }));
  };

  //      
  const addResourceField = () => {
    setFormData(prev => ({
      ...prev,
      resources: [...prev.resources, '']
    }));
  };

  //      (    )
  const removeResourceField = (index) => {
    if (formData.resources.length > 1) {
      const newResources = formData.resources.filter((_, i) => i !== index);
      setFormData(prev => ({
        ...prev,
        resources: newResources
      }));
    }
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (isFormValid) {
      setIsSubmitting(true);
      
      //   
      await new Promise(resolve => setTimeout(resolve, 800));
      
      //     
      const cleanedData = {
        ...formData,
        resources: formData.resources.filter(resource => resource.trim() !== '')
      };
      
      onSave(cleanedData);
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="technology-form" noValidate>
      {/*    -    */}
      <div
        role="status"
        aria-live="polite"
        aria-atomic="true"
        className="sr-only"
      >
        {isSubmitting && ' ...'}
      </div>

      <h2>{initialData.title ? ' ' : '  '}</h2>

      {/*   */}
      <div className="form-group">
        <label htmlFor="title" className="required">
           
        </label>
        <input
          id="title"
          name="title"
          type="text"
          value={formData.title}
          onChange={handleChange}
          className={errors.title ? 'error' : ''}
          placeholder=": React, Node.js, TypeScript"
          aria-describedby={errors.title ? 'title-error' : undefined}
          aria-invalid={!!errors.title}
          aria-required="true"
          required
        />
        {errors.title && (
          <span id="title-error" className="error-message" role="alert">
            {errors.title}
          </span>
        )}
      </div>

      {/*   */}
      <div className="form-group">
        <label htmlFor="description" className="required">
          
        </label>
        <textarea
          id="description"
          name="description"
          value={formData.description}
          onChange={handleChange}
          rows="4"
          className={errors.description ? 'error' : ''}
          placeholder=",        ..."
          aria-describedby={errors.description ? 'description-error' : undefined}
          aria-invalid={!!errors.description}
          aria-required="true"
          required
        />
        {errors.description && (
          <span id="description-error" className="error-message" role="alert">
            {errors.description}
          </span>
        )}
      </div>

      {/*   */}
      <div className="form-group">
        <label htmlFor="category"></label>
        <select
          id="category"
          name="category"
          value={formData.category}
          onChange={handleChange}
        >
          <option value="frontend">Frontend</option>
          <option value="backend">Backend</option>
          <option value="database"> </option>
          <option value="devops">DevOps</option>
          <option value="mobile"> </option>
          <option value="tools"></option>
          <option value="other"></option>
        </select>
      </div>

      {/*   */}
      <div className="form-group">
        <label htmlFor="difficulty"></label>
        <select
          id="difficulty"
          name="difficulty"
          value={formData.difficulty}
          onChange={handleChange}
        >
          <option value="beginner"></option>
          <option value="intermediate"></option>
          <option value="advanced"></option>
        </select>
      </div>

      {/*  */}
      <div className="form-group">
        <label htmlFor="deadline"> ()</label>
        <input
          id="deadline"
          name="deadline"
          type="date"
          value={formData.deadline}
          onChange={handleChange}
          className={errors.deadline ? 'error' : ''}
          aria-describedby={errors.deadline ? 'deadline-error' : undefined}
          aria-invalid={!!errors.deadline}
        />
        {errors.deadline && (
          <span id="deadline-error" className="error-message" role="alert">
            {errors.deadline}
          </span>
        )}
      </div>

      {/*     */}
      <div className="form-group">
        <label>  </label>
        {formData.resources.map((resource, index) => (
          <div key={index} className="resource-field">
            <input
              type="url"
              value={resource}
              onChange={(e) => handleResourceChange(index, e.target.value)}
              placeholder="https://example.com"
              className={errors[`resource_${index}`] ? 'error' : ''}
              aria-describedby={errors[`resource_${index}`] ? `resource-${index}-error` : undefined}
              aria-invalid={!!errors[`resource_${index}`]}
            />
            {formData.resources.length > 1 && (
              <button
                type="button"
                onClick={() => removeResourceField(index)}
                className="btn-remove"
                aria-label={`  ${index + 1}`}
              >
                
              </button>
            )}
            {errors[`resource_${index}`] && (
              <span id={`resource-${index}-error`} className="error-message" role="alert">
                {errors[`resource_${index}`]}
              </span>
            )}
          </div>
        ))}
        <button
          type="button"
          onClick={addResourceField}
          className="btn-add-resource"
        >
          +  
        </button>
      </div>

      {/*   */}
      <div className="form-actions">
        <button
          type="submit"
          className="btn-primary"
          disabled={!isFormValid || isSubmitting}
          aria-busy={isSubmitting}
        >
          {isSubmitting ? '...' : ''}
        </button>
        <button
          type="button"
          onClick={onCancel}
          className="btn-secondary"
          disabled={isSubmitting}
        >
          
        </button>
      </div>
    </form>
  );
}

export default TechnologyForm;