import { useState } from 'react';
import './DataImportExport.css';

function DataImportExport({ technologies, onImport }) {
  const [status, setStatus] = useState('');
  const [isDragging, setIsDragging] = useState(false);

  //    JSON-
  const exportToJSON = () => {
    try {
      //    JSON-  
      const dataStr = JSON.stringify(technologies, null, 2);

      //  Blob   
      const dataBlob = new Blob([dataStr], { type: 'application/json' });

      //     
      const url = URL.createObjectURL(dataBlob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `technologies_${new Date().toISOString().split('T')[0]}.json`;

      //       
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);

      //  
      URL.revokeObjectURL(url);

      setStatus('   JSON ');
      setTimeout(() => setStatus(''), 3000);
    } catch (error) {
      setStatus('  ');
      console.error(' :', error);
    }
  };

  //    JSON-
  const importFromJSON = (event) => {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();

    //    
    reader.onload = (e) => {
      try {
        const imported = JSON.parse(e.target.result);

        //     -  
        if (!Array.isArray(imported)) {
          throw new Error('  ');
        }

        //   
        const isValid = imported.every(item => 
          item.title && 
          typeof item.title === 'string' &&
          item.category &&
          typeof item.category === 'string'
        );

        if (!isValid) {
          throw new Error('  ');
        }

        onImport(imported);
        setStatus(` ${imported.length} `);
        setTimeout(() => setStatus(''), 3000);
      } catch (error) {
        setStatus(' :   ');
        console.error(' :', error);
      }
    };

    //      
    reader.readAsText(file);

    //   input       
    event.target.value = '';
  };

  //  drag-and-drop
  const handleDragOver = (e) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = () => {
    setIsDragging(false);
  };

  const handleDrop = (e) => {
    e.preventDefault();
    setIsDragging(false);

    const file = e.dataTransfer.files[0];
    if (file && file.type === 'application/json') {
      //         importFromJSON
      const reader = new FileReader();
      reader.onload = (event) => {
        try {
          const imported = JSON.parse(event.target.result);
          if (Array.isArray(imported)) {
            onImport(imported);
            setStatus(` ${imported.length}  ()`);
            setTimeout(() => setStatus(''), 3000);
          }
        } catch (error) {
          setStatus(' :   ');
        }
      };
      reader.readAsText(file);
    } else {
      setStatus(',  JSON ');
      setTimeout(() => setStatus(''), 3000);
    }
  };

  return (
    <div className="data-import-export">
      <h2>   </h2>
      
      {/*   */}
      <div 
        className="status-message" 
        role="status"
        aria-live="polite"
        aria-atomic="true"
      >
        {status}
      </div>

      <div className="controls">
        <button 
          onClick={exportToJSON} 
          disabled={technologies.length === 0}
          className="btn-export"
          aria-label="   JSON "
        >
             JSON
        </button>

        <label className="file-input-label" aria-label="   JSON ">
          <input
            type="file"
            accept=".json"
            onChange={importFromJSON}
            aria-label=" JSON   "
          />
          <span className="btn-import">   JSON</span>
        </label>
      </div>

      <div
        className={`drop-zone ${isDragging ? 'dragging' : ''}`}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        role="region"
        aria-label="   "
        tabIndex="0"
      >
        <p>  JSON- </p>
        <p className="hint">    </p>
      </div>

      {technologies.length > 0 && (
        <div className="info">
          <p><strong> :</strong> {technologies.length}</p>
          <p><strong> :</strong>     title, description, category, status  ..</p>
        </div>
      )}
    </div>
  );
}

export default DataImportExport;